.table {
  border-spacing: 1;
  border-collapse: collapse;
  background: #fff;
  border-radius: 6px;
  max-width: 800px;
  width: 100%;
  margin: 0 auto;
  position: relative;

  &__head > tr {
    height: 60px;
    background: #ffed86;
    font-size: 16px;
  }

  &__body > tr {
    height: 48px;
    border-bottom: 1px solid #e3f1d5;
    text-align: center;
  }
  
  &__select {
    height: 30px;
    border-style: hidden;
    border-radius: 6px;
    cursor: pointer;
  }
  &__button {
    width: 30px;
    height: 30px;
    background-color: #f07167;
    border-style: hidden;
    border-radius: 6px;
    cursor: pointer;
  }
}
